  body h1 {
    width: fit-content;
    background-image: linear-gradient(to right, #232ED1, #101D42);
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    color: transparent;
    font-family: "Bangers", cursive;
    font-size: 3em; }
  body p {
    font-family: "Yanone Kaffeesatz", sans-serif; }
  body a {
    text-decoration: none;
    font-family: "Yanone Kaffeesatz", sans-serif;
    color: #232ED1; }

::-webkit-scrollbar {
  width: 0.5em; }

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }

::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey; }

::-moz-scrollbar {
  width: 0.5em; }

::-moz-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }

::-moz-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey; }

.primary-button {
  width: 300px;
  font-size: 1.3em;
  margin: 0.5em 0;
  padding: 0.3em 0.5em;
  font-family: "Pacifico", cursive;
  text-transform: capitalize;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
  background-image: linear-gradient(to right, #232ED1, #101D42);
  color: white; }
  .primary-button:hover {
    transform: translateY(-2px);
    -webkit-box-shadow: 10px 10px 37px -14px #101d42;
    -moz-box-shadow: 10px 10px 37px -14px #101d42;
    box-shadow: 10px 10px 37px -14px #101d42; }
  .primary-button:active {
    transform: translateY(2px);
    -webkit-box-shadow: 10px 10px 12px -8px rgba(16, 29, 66, 0.63);
    -moz-box-shadow: 10px 10px 12px -8px rgba(16, 29, 66, 0.63);
    box-shadow: 10px 10px 12px -8px rgba(16, 29, 66, 0.63); }

.danger-button {
  width: 300px;
  font-size: 1.3em;
  margin: 0.5em 0;
  padding: 0.3em 0.5em;
  font-family: "Pacifico", cursive;
  text-transform: capitalize;
  cursor: pointer;
  transition: all 0.2s;
  background-color: #D72638;
  border: none;
  color: white; }
  .danger-button:hover {
    transform: translateY(-2px);
    -webkit-box-shadow: 10px 10px 37px -14px #d72638;
    -moz-box-shadow: 10px 10px 37px -14px #d72638;
    box-shadow: 10px 10px 37px -14px #d72638; }
  .danger-button:active {
    transform: translateY(2px);
    -webkit-box-shadow: 10px 10px 12px -8px rgba(215, 38, 56, 0.63);
    -moz-box-shadow: 10px 10px 12px -8px rgba(215, 38, 56, 0.63);
    box-shadow: 10px 10px 12px -8px rgba(215, 38, 56, 0.63); }

.form-group {
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 300px; }
  .form-group h3 {
    font-family: "Bangers", cursive;
    font-size: 1.6em;
    color: #232ED1; }
  .form-group .errors {
    margin: 1em 0; }
    .form-group .errors .error {
      background-color: #D72638;
      padding: 0.3em 0.4em;
      color: #efa7ae;
